body {
	@include got;
	font-size: 16px;
	line-height: 1.375;
	color: $white;
	@include tablet {
		font-size: 14px; } }

.out {
	position: relative;
	overflow: hidden; }


.bg-red {
	background-color: $red; }

.d-flex {
	display: flex; }

.logo {
	display: flex;
	align-items: flex-start;
	max-width: 266px;
	@include md {
		max-width: 207px; }
	@include sm {
		max-width: 158px; }
	img {
		width: 100%; } }

.title {
	@include gotM;
	font-size: 60px;
	letter-spacing: 0.4px;
	line-height: 1.07;
	@include tablet {
		font-size: 60px; }
	@include md {
		font-size: 44px;
		//line-height: 1.37
		line-height: 1.1;
		letter-spacing: 0.22px; } }

.wrapper {
	max-width: 1380px;
	margin: 0 auto;
	padding: 0 50px;
	@include tablet {
		max-width: 850px;
		padding: 0 20px; }
	@include md {
		padding: 0 10vw; }
	@include xxs {
		padding: 0 14px; } }
.color-yellow {
	color: $yellow; }


.descr {
	font-size: 18px;
	line-height: 2;
	@include tablet {
		font-size: 16px; }
	@include md {
		font-size: 14px; }
	p {
		&:not(:last-child) {
			margin-bottom: 18px;
			@include md {
				margin-bottom: 14px; } } } }

.link {
	color: inherit;
	display: inline-flex;
	align-items: center;
	.icon {
		fill: $white;
		margin-right: 9px;
		font-size: 26px;
		vertical-align: sub;
		@include tablet {
			font-size: 24px; }
		@include md {
			font-size: 19px;
			margin-right: 4px; } } }
.title-img {
	padding: 40px 0;
	img {
		max-width: 100%;
		margin: 0 auto;
		display: block; } }
