.schedule {
	padding-top: 120px;
	background: $grayL;
	@include tablet {
		padding-top: 72px; }
	@include md {
		padding-top: 32px; }
	&__title {
		color: $red;
		margin-bottom: 51px;
		@include tablet {
			margin-bottom: 46px; }
		@include md {
			margin-bottom: 38px; }
		@include xxs {
			padding: 0 14px; } }
	&> .wrapper {
		@include xxs {
			padding: 0; } } }




.tabs {
	&__header {
		display: flex;
		justify-content: space-between;
		margin-bottom: 40px;
		@include tablet {
			margin-bottom: 19px; }
		@include md {
			display: block;
			margin-bottom: 29px; }
		@include xxs {
			padding: 0 14px; }
		li {
			width: 100%;
			max-width: 33.3%;
			&:not(:last-child) {
				margin-right: 20px;
				@include md {
					margin-bottom: 20px; } }
			@include tablet {
				max-width: 33%; }
			@include md {
				max-width: 100%; }
			@include sm {
				margin-bottom: 28px; }
			.btn {
				@include gotM;
				color: $black;
				padding-top: 32px;
				padding-bottom: 28px;
				text-transform: uppercase;
				background: transparent;
				border-bottom: 4px solid $white;

				&:hover {
					color: $red; }

				@include tablet {
					font-size: 16px;
					padding: 17px 10px; }
				@include md {
					padding-top: 10px;
					padding-bottom: 10px; }
				&.is-active {
					border-color: $red;
					color: $red;
					pointer-events: none;
					background-color: transparent;
					&:before {
						display: none; } } } } }
	&__content {
		.table {
			&:not(.is-active) {
				display: none; } } } }

