.hero {
	//min-height: 1033px
	min-height: 100vh;
	padding: 156px 58px 120px;
	display: flex;
	flex-direction: column;
	position: relative;

	@media screen and (min-width: 1024px) and (max-height: 800px) {
		padding: 90px 58px 60px; }

	@include tablet {
		// padding: 90px 58px 60px
		padding: 156px 58px 120px; }

	@include md {
		// padding: 45px 30px 45px
		padding: 90px 58px 60px; }
	@include sm {
		padding: 45px 40px 15px; }
	@include xs {
		padding: 15px 15px 15px; }


	&:after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: url(../img/overlay.svg);
		background: rgba(0, 0, 0, .45);
		z-index: 1;

		@include sm {
			background: url(../img/overlay_small.svg); } }


	&__inner {
		position: relative;
		max-width: 1280px;
		margin: 0 auto;
		width: 100%;
		display: flex;
		flex-direction: column;
		flex: 1;
		z-index: 2; }

	&__logo {
		margin-bottom: 0;

		@include md {
			margin: 23px 0 46px; }
		@include sm {
 }			// margin-bottom: 57px
		@include xs {
			margin: 0 0 49px; } }

	&__content {
		display: flex;
		justify-content: space-between;
		margin: auto 0;

		@include md {
			margin: 0;
			margin: auto 0;
			position: relative;
			top: -36px; }

		@include sm {
			margin: auto 0;
			flex-direction: column;
			top: 0; } }

	&__left {
		text-align: left;
		max-width: 640px;
		margin-right: 40px;

		@include sm {
			text-align: center;
			max-width: none;
			margin-right: 0; }
		@include xs {
			text-align: left; } }

	&__right {
		flex: 0 0 auto;
		@include md; }

	&__bottom {
		display: flex;
		justify-content: space-between;

		@include md {
			margin: auto 0 0; }

		@include xs {
			flex-direction: column-reverse;
			justify-content: center; } }

	&__title {
		@include gotM;
		margin: 0 0 50px;
		font-size: 48px;
		line-height: 1.26;
		letter-spacing: -0.005em;
		display: block;
		padding: 0 0 0 102px;

		@media screen and (max-width: 1100px) {
			padding-left: 50px; }

		@include md {
			font-size: 40px;
			padding: 0 0 0 79px;
			margin: 0 0 36px; }

		@include md {
			// font-size: 34px
 }			// padding: 0 0 0 30px

		@include sm {
			font-size: 34px;
			margin-bottom: 16px;
			padding-left: 0; }

		@include xs {
			font-size: 24px;
			margin-bottom: 36px;
			line-height: 1.34;
			padding-left: 38px; } }
	&__descr {
		@include got;
		font-size: 18px;
		line-height: 2;
		margin-bottom: 28px;
		@include md {
			font-size: 14px;
			max-width: 500px; }

		@include sm {
			font-size: 16px;
			line-height: 1.6;
			margin-bottom: 15px;
			max-width: none; }

		@include xs {
			font-size: 14px;
			line-height: 1.76;
			margin-bottom: 34px; } }


	&__techs-title {
		font-size: 18px;
		line-height: 1.6;
		margin-bottom: 100px;
		padding-top: 20px;

		@include tablet;

		@include md {
			font-size: 18px;
			line-height: 1.5;
			margin-bottom: 68px;
			padding-top: 10px; }
		@include sm {
			text-align: center;
			font-size: 14px;
			line-height: 1.6;
			margin-bottom: 42px; }
		@include xs; }


	&__btn {
		.btn {
			height: 66px;
			padding: 0 30px;
			@include gotM; }
		@include sm {
			//padding: 0 10px
			.btn {
				min-width: 247px;
				width: 100%; } }
		@include xs {
			margin: 40px 0 15px; } }


	&_min {
		min-height: auto;
		padding: 110px 0 100px;
		background-position: center;
		background-size: cover;

		&:after {
			display: none; }
		@include md {
			padding: 94px 20px; }
		@include sm {
			padding: 44px 20px; }
		@include xs {
			padding: 44px 20px; }
		.hero {
			&__logo {
				margin: 20px auto 60px;
				@include md {
					margin-bottom: 20px; } }
			&__title {
				font-size: 72px;
				max-width: 836px;
				padding: 0;
				margin: 0 auto;
				line-height: 1.1;
				@include tablet {
					font-size: 60px; }
				@include md {
					font-size: 44px; }
				@include sm {
					font-size: 32px; } } } }

	&__video {
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 0;
		object-fit: cover; } }

.intro {
	z-index: 1;
	position: relative;
	overflow: hidden; }
