.info {
	display: flex;
	@include md {
		margin: 0 auto; }

	&__icon {
		font-size: 82px;

		@include tablet;


		@include sm {
			font-size: 51px;
			display: flex; }

		.icon {
			fill: $yellow; }
		&:not(:last-child) {
			margin-right: 30px;
			@include md {
				margin-right: 19px; } } }

	&__num {
		@include gotL;
		font-size: 88px;
		line-height: 1;
		min-width: 210px;
		text-align: right;
		padding: 0 24px 0 0;

		@include md {
			font-size: 74px;
			line-height: 1.05;
			padding: 0 16px 0 0;
			min-width: 180px; }

		@include sm {
			font-size: 72px;
			min-width: 0; }
		@include xs {
			font-size: 62px;
			padding-right: 10px; } }

	&__content {
		padding-top: 8px;
		// min-width: 120px
		@include sm {
			padding-top: 0;
			margin-top: 5px; }
		@include xs {
			margin-top: 8px;
			min-width: 80px; } }
	&__title {
		@include gotL;
		font-size: 18px;
		letter-spacing: 0.11px;
		line-height: 1;
		margin: 0 0 12px;
		@include md {
			margin: 0 0 8px;
			font-size: 16px; }
		@include sm {
			font-size: 20px;
			margin: 0 0 4px; }
		@include xs {
			font-size: 14px; }

		// &:not(:last-child)
		//argin-bottom: 10px
		//include md
		//margin-bottom: 4px
		//include xs
 }		//margin-bottom: 5px
	&__descr {
		@include gotL;
		font-size: 14px;
		line-height: 1.48;
		@include md {
			font-size: 12px; }
		@include sm {
			font-size: 14px;
			line-height: 1.3; }
		@include xs {
			font-size: 12px;
			br {
				display: block; } } } }




.info-wrapper {
	padding: 42px 0 24px;
	@include md {
		padding: 24px 0; }
	&__inner {
		display: flex;
		justify-content: center;
		@include xs {
			flex-direction: column;
			align-items: center; }
		.info {
			&:not(:last-child) {
				margin-right: 106px;
				@include sm {
					margin-right: 50px; }
				@include xs {
					margin: 0 0 20px; } } } } }
